body{
  padding: 0;
  margin: 0;

}

a{
  text-decoration: none;
  color:white;
}

@font-face{
  font-family: Emoona;
  src:url("font/font/Avenir-Light.ttf");

}

@font-face{
  font-family: EmoonaMedium;
  src:url("font/font/Avenir-Medium.ttf");

}

@font-face{
  font-family: EmoonaHeavy;
  src:url("font/font/Avenir-Heavy.ttf");

}



.main-wrapper{
  overflow: hidden;
  background-color: #eeeff1;
  color: #2e2e2e;
  height: 100vh;
}



.text-container{
  overflow: hidden;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height:100%;
}

.text-container #scene {
  width:100%;
  height:100vh;
  overflow: hidden;
}


.text{
  font-family:Emoona;
  display: block;
}

.text1{
  font-size: 100px;
  margin-bottom: -20px;
}

.text2{
  font-size: 30px;
}

.button{
  margin-top: 10px;
}

.scene{
  position: absolute;
  width: 100%;
  left:0;
  top:0;

}

.obj {
  width: 250px;
  height: 180px;
}

.obj img{
  width: 250px;
  height: 250px;
}

.level1{
  position: absolute;
  display: block;

